@import 'ScssHelpers/helpers-all.scss';

.venue_direction {
	@include bp($point_1, min) {
		padding: 0;
	}

	@include bp($point_1 - 1) {
		padding: 5.5rem 0 0;
	}

	&.venue_direction--visit_page_mod {
		@include bp($point_2, min) {
			padding: 10rem 0 0;
		}

		@include bp($point_3) {
			padding: 0;
		}
	}
}

.section_wrap {
	position: relative;
	@include bp($point_1, min) {
		padding: 18rem var(--gl-indent) 13rem;
	}

	@include bp($point_1 - 1, $point_6) {
		padding: 13.6rem var(--gl-indent) 8rem;
	}

	@include bp($point_6 - 1) {
		padding: 7.6rem var(--gl-indent) 8rem;
	}

	.venue_direction--visit_page_mod & {
		@include bp($point_3) {
			padding: 4.6rem var(--gl-indent) 8rem;
			background-color: $black_2;
		}
	}
}

.venue_direction__in {
	max-width: 152rem;
}

.venue_direction__decor_picture {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;

	display: block;
	width: 100%;
	height: 100%;

	overflow: hidden;

	&:before {
		position: absolute;
		left: 0;

		z-index: 0;

		width: 100%;
		height: 100%;

		background-color: $black_2;

		content: '';

		@include bp($point_5, min) {
			top: 17.4vw;
		}
		@include bp($point_5 - 1) {
			top: 21.4vw;
		}
	}
}

.venue_direction__decor_img {
	@include cover_img;
	height: unset;
	object-position: top center;

	@include bp($point_5) {
		display: none;
	}
}

.venue_direction__decor_img_mob {
	@include cover_img;
	height: unset;
	object-position: top center;

	@include bp($point_5, min) {
		display: none;
	}
	@include bp($point_5 - 1) {
		display: block;
	}
}

.venue_direction__head {
	text-align: center;
	@include bp($point_1, min) {
		width: 42vw;
		margin: 0 auto;

		&:not(:last-child) {
			margin-bottom: 8.4rem;
		}
	}

	@include bp($point_3, $point_4) {
		margin: 0 auto 6.6rem;
	}

	@include bp($point_1 - 1, $point_4) {
		width: 68vw;
	}

	@include bp($point_1 - 1, $point_2) {
		margin: 0 auto 2.6rem;
	}

	@include bp($point_4 - 1) {
		width: 74vw;
		margin: 0 auto 2.6rem;
	}

	.venue_direction--visit_page_mod & {
		@include bp($point_2, min) {
			margin-bottom: 4.8rem;
		}
		@include bp($point_3, $point_4) {
			margin: 0 auto 2.6rem;
		}
	}
}

.venue_direction__title {
	@include h2;

	font-family: $font_2;
	color: $white_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			width: 80%;
			margin: 0 auto 1rem;
		}
	}
}

.venue_direction__text {
	@include text;

	color: $white_2;

	@include bp($point_1, min) {
		font-size: 2rem;
		font-weight: 500;
	}
}

//---------------------------------- DROPDOWN

.venue_direction__dropdown {
	position: relative;

	width: max-content;

	margin: 0 auto;

	display: none;

	.venue_direction--visit_page_mod & {
		@include flex_block(row, wrap, center, flex-start, flex-start);

		&.venue_direction__dropdown--tab_content_active {
			&:not(:last-child) {
				@include bp($point_1, min) {
					margin-bottom: 7.8rem;
				}

				@include bp($point_1 - 1) {
					margin-bottom: 4.6rem;
				}
			}
		}
	}

	&:after {
		position: absolute;

		z-index: 1;

		font-size: 2rem;
		color: $white_2;

		content: '\25BC';

		pointer-events: none;
		transition: transform ease 0.3s, color 0.3s ease;

		@include bp($point_2, min) {
			top: 3rem;
			right: 2.2rem;
		}
		@include bp($point_3) {
			top: 2.2rem;
			right: 2.4rem;
		}
	}

	&:hover::after {
		color: $black_2;
	}

	&.venue_direction__dropdown--open_state::after {
		color: $black_2;
		transform: rotate(180deg);
	}
}

.venue_direction__dropdown_head {
	position: relative;

	display: block;

	font-family: $font_2;
	font-weight: 700;
	color: $white_2;

	text-align: left;

	border: 2px solid $white_2;
	border-radius: 0.8rem;
	background: transparent;

	appearance: none;
	-webkit-appearance: none;
	transition: background 0.3s ease, border-color 0.3s ease, color 0.3s ease;

	option {
		font-family: $font_1;
		font-size: 2rem;
		color: $black_2;

		background-color: $white;

		&[disabled] {
			background-color: $gray;
		}
	}

	// span {
	// 	@include transition_color;

	// 	font-family: $font_2;
	// 	font-weight: 700;
	// 	color: $white_2;

	// 	pointer-events: none;

	// 	@include bp($point_2, min) {
	// 		font-size: 3.2rem;
	// 	}

	// 	@include bp($point_3) {
	// 		font-size: 2.4rem;
	// 	}
	// }

	@include bp($point_2, min) {
		min-width: 37.2rem;
		min-height: 8rem;

		padding: 0 5rem 0 2.4rem;

		font-size: 3.2rem;
		line-height: 8rem;
	}

	@include bp($point_3) {
		width: 91%;
		padding: 1.8rem 4rem 1.4rem 2.4rem;
		font-size: 2.4rem;
	}

	&:hover {
		text-decoration: none;
		background: $white_2;
		border-color: transparent;
		color: $black;

		// span {
		// 	color: $black;
		// }
	}

	.venue_direction__dropdown--open_state & {
		background-color: $white;
		border-radius: 0.8rem 0.8rem 0 0;
		border-color: $green;
		border-bottom: none;

		@include bp($point_1, min) {
			padding-bottom: 2.2rem;
		}

		@include bp($point_1 - 1) {
			padding-bottom: 1.6rem;
		}

		span {
			color: $black;
		}
	}
}

