//----------------------------- CONTENT

.venue_direction__holder {
	width: 100%;
}

.venue_direction__wrap {
	display: none;
	overflow: hidden;

	&:nth-child(1) {
		@include flex_block(row, wrap, flex-start, center, flex-start);
	}

	.venue_direction--visit_page_mod & {
		display: none;
	}

	&.venue_direction__dropdown--tab_content_active {
		@include bp($point_1, min) {
			@include flex_block(row, wrap, flex-start, center, flex-start);
		}
		@include bp($point_3) {
			display: block;
		}
	}
}

.venue_direction__col {
	@include bp($point_1, min) {
		&:nth-child(1) {
			width: 52.4%;
		}

		&:nth-child(2) {
			width: 47.6%;
		}
	}

	@include bp($point_1 - 2) {
		width: 100%;
	}
}

.venue_direction__picture {
	position: relative;
	display: block;

	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		padding-top: 85%;
	}
	@include bp($point_3) {
		padding-top: 69%;
	}
}

.venue_direction__img {
	@include contain_img;

	position: absolute;
	top: 0;
	left: 0;
}

.venue_direction__content {
	color: $white_2;

	@include bp($point_1, min) {
		padding: 3rem 5rem 3rem min(8vw, 14.6rem);
	}

	@include bp($point_1 - 1) {
		padding: 3.4rem 0 0 2rem;
	}

	h3 {
		font-family: $font_2;
		font-weight: 400;
		font-style: italic;

		@include bp($point_1, min) {
			font-size: 3.2rem;

			&:not(:last-child) {
				margin-bottom: 0.8rem;
			}
		}

		@include bp($point_1 - 1) {
			font-size: 2.4rem;

			&:not(:last-child) {
				margin-bottom: 0.6rem;
			}
		}
	}

	p {
		@include text;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	ol {
		counter-reset: item;

		&:not(:last-child) {
			@include bp($point_1, min) {
				margin-bottom: 4.2rem;
			}

			@include bp($point_1 - 1) {
				margin-bottom: 3.6rem;
			}
		}

		li {
			position: relative;

			padding-left: 2.8rem;

			@include bp($point_1, min) {
				&:not(:last-child) {
					margin-bottom: 1.2rem;
				}
			}

			@include bp($point_1 - 1) {
				padding-left: 2.4rem;
			}
		}

		p {
			&:before {
				position: absolute;
				left: 0.4rem;
				top: 0;
				z-index: 0;

				counter-increment: item;
				content: counter(item) '.';
			}
		}
	}
}

.venue_direction__content_btn_wrap {
	padding-right: 2rem;
}

.venue_direction__content_btn {
	&:hover,
	&:focus {
		background-color: $white_2;
		color: $black_2;
	}

	@include bp($point_1 - 1) {
		background-color: $coral;
		color: $black_2;
		border-color: $black_2;

		&:hover,
		&:focus {
			background-color: $black_2;
			color: $white_2;
			border-color: $white_2;
		}
	}

	.venue_direction--visit_page_mod & {
		background-color: $black_2;
		color: $white_2;
		border-color: $white_2;

		@include bp($point_1 - 1) {
			width: 100%;
		}

		&:hover,
		&:focus {
			background-color: $white_2;
			color: $black_2;
		}
	}
}
